import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import Wiki from './views/Wiki.vue'
import NotFound from './views/NotFound.vue'
import WikiItem from './views/WikiItem.vue'
import About from './views/About.vue'


Vue.use(Router)

export default new Router({
 /// mode: 'history',
  base: process.env.BASE_URL,
  routes: [
{
      path: '/',
      name: '',
      component: Home
    },
    {
      path: '/wiki',
      name: 'wiki',
      component: Wiki,
    },
        {
      path: '/about',
      name: 'about',
      component: About,
    },
    {
      path: '/wikiItem',
      name: 'wikiItem',
      component: WikiItem,
      props: route => ({
        name: route.query.name,
        desc: route.query.desc,
        type: route.query.ctype,
        amount: route.query.camount,
        icon: route.query.cicon,


        x11: route.query.c11,
        x12: route.query.c12,
        x13: route.query.c13,
        x21: route.query.c21,
        x22: route.query.c22,
        x23: route.query.c23,
        x31: route.query.c31,
        x32: route.query.c32,
        x33: route.query.c33,
         })
  
    },

    { path: '*', component: NotFound }
  ]
})
